import React from "react";
import { useHistory } from "react-router-dom";
import { CartStyled } from "./style";
import { getStatus } from "../../../utils";

const Cart = ({
  causeOfFactor,
  organ,
  region,
  economicActivity,
  id,
  fullname,
  status,
}) => {
  const history = useHistory();

  return (
    <CartStyled
      onClick={() => history.push(`/request/${id}`)}
      color={getStatus(" ")}
    >
      <div className="index">
        <p>{id}</p>
      </div>
      <div className="header">
        <p> </p>
      </div>
      <div className="description">
        <p>{causeOfFactor.title}</p>
      </div>
      <div className="header">
        <p>
          /  ,   
           
        </p>
      </div>
      <div className="description">
        <p>{fullname}</p>
      </div>
      <div className="header">
        <p>  </p>
      </div>
      <div className="description">
        <p>{economicActivity.title}</p>
      </div>
      <div className="header">
        <p> (. /)</p>
      </div>
      <div className="description">
        <p>{region.title}</p>
      </div>
      <div className="header">
        <p>  </p>
      </div>
      <div className="description">
        <p>{organ.title}</p>
      </div>
      <div className="header">
        <p>   </p>
      </div>
      <div className={"status + " + getStatus(" ")}>
        <p>{status.title}</p>
      </div>
    </CartStyled>
  );
};

export default Cart;
