import { Field, reduxForm } from "redux-form";
import { SelectForm } from "../../../../common";
import { STYLES } from "../../../../constains";
import { Button } from "../../../../styledComponents";
import Select from "../Select";

const typesData = [
    { value: 1, label: "" },
    { value: 2, label: " ,  , " },
    { value: 3, label: " " },
    { value: 4, label: ",  " },
    { value: 5, label: "" },
    { value: 6, label: "" },
    { value: 7, label: "" },
    { value: 8, label: "" },
]

const organData = [
    { value: 1, label: " " },
    { value: 2, label: " " },
    { value: 3, label: " " }
]


const Form = ({ ECONOMIC, handleSubmit }) => {

    return (
        <form onSubmit={handleSubmit}>
            <Field name="select" options={typesData} placeholder="  " component={SelectForm} />
            <Field name="select" options={organData} placeholder="  " component={SelectForm} />
            <Button
                color="white"
                margin="0 0 10px"
                bg={STYLES.blue}
                padding="9px 53px"
            >
                
            </Button>

        </form>
    )
}

const StatisticsForm = reduxForm({
    form: 'statistics'
})(Form)

export default StatisticsForm