import React from "react";
import { Field, reduxForm } from "redux-form";
import {
  validField,
  validFieldMin,
  validFieldMax,
  errorScroll,
  changeSelect,
} from "../../../utils";
import {
  InputForm,
  TextareaForm,
  SelectForm,
  FileForm,
  CheckboxForm,
  Condition,
} from "../../../common";
import {
  ECONOMIC,
  ROLES,
  CONDITIONS,
  OPTIONS,
  STYLES,
} from "../../../constains";
import FormRequest from "../FormStyles";
import { Button, Label } from "../../../styledComponents";

const minLength = validFieldMin(2);
const maxLengthInput = validFieldMax(30);
const maxLengthTextarea = validFieldMax(1000);

const Form = ({
  handleSubmit,
  routeRole,
  activity,
  region,
  organ,
  user,
  cause,
  subject,
}) => {
  return (
    <FormRequest onSubmit={handleSubmit}>
      <Label>:</Label>
      <Field
        options={ROLES}
        component={SelectForm}
        name="applicantType"
        onChange={routeRole}
        defaultValue={ROLES[0]}
      />
      <Label>:</Label>
      <Field
        component={InputForm}
        name="fullname"
        validate={[validField, maxLengthInput, minLength]}
        placeholder=" "
      />
      <Label> :</Label>
      <Field
        component={InputForm}
        name="companyName"
        validate={[validField, maxLengthInput, minLength]}
        placeholder="  "
      />
      <Label> :</Label>
      <Field
        component={InputForm}
        name="telephone"
        type="tel"
        validate={[validField, maxLengthInput, minLength]}
        placeholder="  "
      />
      <Label> :</Label>
      <Field
        component={InputForm}
        name="email"
        validate={[validField, maxLengthInput, minLength]}
        placeholder="  "
        type="email"
      />
      <Label>  :</Label>
      <Field
        name="subjectTypeId"
        component={SelectForm}
        options={changeSelect(subject)}
        validate={[validField]}
        placeholder="  "
      />
      <Label>    :</Label>
      <Field
        name="economicActivityId"
        component={SelectForm}
        options={changeSelect(activity)}
        validate={[validField]}
        placeholder="   "
      />
      <Label>    :</Label>
      <Field
        name="organId"
        component={SelectForm}
        options={changeSelect(organ)}
        validate={[validField]}
        placeholder=" "
      />
      <Label>   :</Label>
      <Field
        name="regionId"
        component={SelectForm}
        options={changeSelect(region)}
        validate={[validField]}
        placeholder=" "
      />
      <Label>  :</Label>
      <Field
        name="causeOfFactor"
        component={SelectForm}
        options={changeSelect(cause)}
        validate={[validField]}
        placeholder="   "
      />
      <Label>  :</Label>
      <Field
        component={TextareaForm}
        name="problemOfDescription"
        validate={[validField, maxLengthTextarea, minLength]}
      />
      <Label>
                
        :
      </Label>
      <Field
        component={TextareaForm}
        name="descriptionDate"
        validate={[validField, maxLengthTextarea, minLength]}
      />
      <Label>        ? </Label>
      <Field
        component={TextareaForm}
        name="clarification"
        validate={[validField, maxLengthTextarea, minLength]}
      />
      <Field component={FileForm} type="file" name="file" />
      <Condition data={CONDITIONS} />
      <div className="request-form--checkbox">
        <Field
          component={CheckboxForm}
          name="agreement"
          label=" "
        />
      </div>
      <Button bg={STYLES.blue} margin="0 auto" color="white">
         
      </Button>
    </FormRequest>
  );
};

const IndividualForm = reduxForm({
  form: "individual",
  onSubmitFail: errorScroll,
})(Form);

export default IndividualForm;
